import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";
function Navbar(props) {
  return (
    <div class="navbar">
      <Link to={props.src}>
        <div class="logo">
          <p>E</p>
        </div>
      </Link>
      <h3>Smart Eng</h3>

      <Link to="/Login" class="login-Link">
        Log IN
      </Link>
      <div class="acc">
        <p>Q</p>
        <div class="dropdown-content">
          <Link to="/">Log out</Link>
          <a href="#">Link 2</a>
          <a href="#">Link 3</a>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
